import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [{
  path: '/',
  redirect: '/login',
  component: resolve => require(['@/App.vue'], resolve)
}, {
  path: '/login',
  component: resolve => require(['@/components/login.vue'], resolve)
}, {
  path: '/Home',
  component: resolve => require(['@/components/Home.vue'], resolve),
  redirect: '/users',
  children: [{
    path: '/users',
    component: resolve => require(['@/components/Users/users.vue'], resolve)
  }, {
    path: '/rights',
    component: resolve => require(['@/components/rights/rights.vue'], resolve)
  }, {
    path: '/roles',
    component: resolve => require(['@/components/rights/roles.vue'], resolve)
  }, {
    path: '/reports',
    component: resolve => require(['@/components/reports/reports.vue'], resolve)
  }, {
    path: '/orders',
    component: resolve => require(['@/components/orders/orders.vue'], resolve)
  }, {
    path: '/goods',
    component: resolve => require(['@/components/goods/goods.vue'], resolve)
  }, {
    path: '/categories',
    component: resolve => require(['@/components/goods/categories.vue'], resolve)
  }, {
    path: '/params',
    component: resolve => require(['@/components/goods/params.vue'], resolve)
  }, {
    path: '/goods/addGoods',
    component: resolve => require(['@/components/goods/addGoods.vue'], resolve)
  }]
}]

const router = new VueRouter({
  routes
})

/* 设置全局路由拦截 */
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem('token')
  if (!token) {
    if (to.path !== '/login') {
      next({
        path: '/login'
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router
